<template>
  <div class="chatbot">
    <div class="chat-window">
      <div v-for="(message, index) in messages" :key="index" class="message" :style="message.sender === 'user' ? 'justify-content: end;' : ''">
        <div :class="['message', message.sender]">
            {{ message.text }}
        </div>
      </div>
    </div>
    <div class="input-area">
      <input
        v-model="userInput"
        @keyup.enter="sendMessage"
        type="text"
        placeholder="Escribe un mensaje..."
      />
      <button @click="sendMessage">Enviar</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      userInput: "",
      messages: [],
    };
  },
  methods: {
    sendMessage() {
      if (this.userInput.trim() !== "") {
        // Agregar el mensaje del usuario
        this.messages.push({
          text: this.userInput,
          sender: "user",
        });

        const userMessage = this.userInput;
        this.userInput = "";

        // Hacer la petición al API usando Axios
        this.getChatbotResponse(userMessage);
      }
    },
    getChatbotResponse(userMessage) {
      const url = 'https://chatpdf.smallpdf.com/v1/collections/d149ac52-642a-4ca4-9296-f6e347152317/query';
      // const url = 'https://chatpdf.smallpdf.com/v1/collections/ef2dd67c-7a6a-4656-af41-360b7ec72d8b/query';
      const payload = {
        query: userMessage,
      };

      axios.post(url, payload)
        .then(response => {
          console.log('Response data:', response.data.response);
          // Agregar la respuesta del chatbot al array de mensajes
          this.messages.push({
            text: response.data.response,
            sender: "bot",
          });
        })
        .catch(error => {
          console.error('Error:', error.response ? error.response.data : error.message);
          // Puedes manejar errores aquí, por ejemplo, mostrando un mensaje de error en la UI
          this.messages.push({
            text: "Lo siento, ocurrió un error.",
            sender: "bot",
          });
        });
    },
  },
};
</script>


<style>
html{
  height: 100%;
}
.chatbot {
  /* max-width: 400px; */
  max-width: 90%;
  margin: 0 auto;
  border: 1px solid #ccc;
  border-radius: 10px;
  overflow: hidden;
  font-family: Arial, sans-serif;
}

.chat-window {
  max-height: 300px;
  overflow-y: auto;
  padding: 10px;
  background-color: #f9f9f9;
}

.message {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin: 0px 0px;
    padding: 10px 10px
}

.message.user {
  background-color: #d1e7dd;
  border-radius: 5px;
  text-align: right;
  -ms-flex: 0 0 100%;
    flex: 0 0 100%;
    width: 100%;
    max-width: 50%;
    justify-content: end;
}

.message.bot {
  background-color: #ffeeba;
  border-radius: 5px;
  -ms-flex: 0 0 100%;
  flex: 0 0 100%;
  width: 100%;
  max-width: 50%;
  text-align: left;
  line-height: 23px;
}

.input-area {
  display: flex;
  padding: 10px;
  background-color: #fff;
}

input[type="text"] {
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button {
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  margin-left: 10px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}
</style>
